<template>
    	<div class="app-container">
		<el-alert title="注意:处理过程约耗时0~5分钟，请稍后查询" show-icon type="warning">
		</el-alert>
		 <inputs-header options=[] :isAdmin="false" @btnClick="checkIccid" class="clearfix"></inputs-header>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad" >
			<el-table-column align="center" prop="number" label="接入号码" width="200">
			</el-table-column>
			<el-table-column align="center" prop="iccid" label="ICCID">
			</el-table-column>
			<el-table-column align="center" prop="state.productStatusName" label="UIM卡状态" width="170">
			</el-table-column>
			<el-table-column align="center" label="操作" width="200">
				<template scope="scope">
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="!(scope.row.state.productStatusCd=='19')" @click="activeStatusAction(scope)">激活</el-button>
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="scope.row.state.productStatusCd=='2'" @click="disabledNumberAction(scope)">停机</el-button>
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="scope.row.state.productStatusCd=='1'" @click="disabledNumberAction(scope)">复机</el-button>
					<el-button type="button" size="small" v-if="!scope.row.operation" disabled>申请处理中</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="ListIotCardState" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>


<script>
    import InputsHeader from '../../../components/InputsHeader/index';
import {
	getListIotCardState,
	getIotCardStateByIccid,
	disabledNumber,
	activateCard
} from '../../../api/userStatus';

	export default {
		data() {
			return {
            options: [],
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			iccidInput: '',
			iccidArr: [],
			isLoad: true
            }
		},
		watch: {},
		mounted() {//页面刷新加载
           this.isLoad = true;
		   this.ListIotCardState(1);

		},
		methods: {
		activeStatusAction(scope) {//激活操作
			this.$confirm('此操作将激活该卡, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then((scope) => {
				console.log("scope", !(scope.row.state.productStatusCd == '19'))
				this.isLoad = true;
				let iccid = scope.row.iccid;
				let number = scope.row.number;
				activateCard({ iccid, number }).then(res => {
					if (res.msg === 0) {
						this.ListIotCardState(1)
						this.$message.success("激活成功")
					} else {
						this.$message.error(res.code)
					}
				}).catch((err) => {
					this.$message.error(err);
				})
				this.isLoad = false;

			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消删除'
				});
			});

		},
		disabledNumberAction(scope) {//停机/复机操作
			let iccid = scope.row.iccid;
			let number = scope.row.number;
			let operation = Object.is(scope.row.state.productStatusCd, 1) ? 19 : 20;
			let operateType = Object.is(scope.row.state.productStatusCd, 1) ? '复机' : '停机';
			this.$confirm('此操作将' + operateType + '该卡, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then((scope) => {
				this.isLoad = true;
				disabledNumber({ iccid, number, operation }).then(res => {
					this.ListIotCardState(1)
					this.$message({
						type: 'success',
						message: operateType + '成功!'
					});
				}).catch((err) => {
					this.$message.error(err);
				})

			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消' + operateType
				});
			});


		},
		checkIccid(iccid) {
			this.isLoad = true;
			if (iccid) {
				this.IotCardStateByIccid(iccid);
			} else {
				this.ListIotCardState(1);
			}
		},
		ListIotCardState(page) {
			getListIotCardState({page})
				.then(res => {
					this.isLoad = false;
					console.log("res", res)
					if (res.msg == 0) {
						this.tableData = res.stateList;
						this.tableCount = res.count;
					} else {
						this.tableData = [];
						this.tableCount = 0;
					}
				});
		},
		IotCardStateByIccid(iccid) {
			this.isLoad=true
			getIotCardStateByIccid({
				iccid: iccid
			})
				.then(res => {
					this.isLoad = false;
					if (res.msg == 0) {
						res.iccid = iccid;
						this.tableData = [res];
						this.tableCount = 1;
					} else {
						this.tableData = [];
						this.tableCount = 0;
						res.msg != 1 ? this.$message.error(backCN(res.code)) : ''
					}
				}).catch(err => {
				this.isLoad = false;
				this.$message.error(backCN(err.status))
			})
		}
	},
	components: {
		InputsHeader
	}
	};
</script>

<style scoped>
    .app-container {
        background: #f5f5f5;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 100%;
        min-width: 637px;
        margin-bottom: 20px;
    }

    .survey-container {

    }

    .survey-item-wrap {
        padding: 5px 0;
    }

    .survey-item {
        border-right: 1px solid #e0e0e0;
    }

    .survey-item-wrap:last-child .survey-item {
        border: none;
    }

    .survey-item h3 {
        font-size: 13px;
        color: #666;
    }

    .survey-item p {
        color: #f88b00;
        font-size: 25px;
    }

    .content-rowspan div {
        padding-left: 13px;
        line-height: 46px;
        border-bottom: 1px solid #ECEDEE;
    }

    .extend .cell {
        padding: 0;
    }

    .flow-details {
        border-bottom: 1px solid #e8e8e8;
    }

    .flow-details:last-child {
        border: none;
    }
</style>



