//用户状态中卡激活
export function activateCard(data){
	console.log("data",data)
  return fetch({
	  url:'/recharge/manager/activateCard.do',
	  method:'post',
		data:data
  })
}
//用户状态中卡停机、复机
export function disabledNumber(data){
  return fetch({
	  url:'/recharge/manager/disabledNumber.do',
	  method:'post',
		data:data
  })
}



